import React from "react";
import { CancelablePromise, Names } from "../api";
import { client } from "../helpers/client";
import { useNavigate } from "react-router-dom";
import {
  Select,
  Input,
  Button,
  Tappable,
  Section,
} from "@telegram-apps/telegram-ui";
import { Icon20Cancel } from "@vkontakte/icons";

type FormDataType = {
  paymentMethod: string;
  fiatCurrency: string;
  cardNumber: string;
  amount: string;
};
const pmNameList: CancelablePromise<Names> = client.pm.getPmNamesList();

const FormPm = () => {
  console.log(pmNameList);
  const navigate = useNavigate();

  const paymentMethods = ["Credit Card", "PayPal", "Bank Transfer"];
  const fiatCurrencies = ["USD", "EUR", "RUB"];

  // Стейт для хранения значений, соответствующих типу FormDataType
  const [formData, setFormData] = React.useState<FormDataType>({
    paymentMethod: paymentMethods[0],
    fiatCurrency: fiatCurrencies[0],
    cardNumber: "",
    amount: "",
  });

  // Обработчики для обновления данных формы
  const handleInputChange = (key: keyof FormDataType, value: string) => {
    setFormData((prevData) => ({
      ...prevData,
      [key]: value,
    }));
  };

  const handleSave = () => {
    console.log(formData);
    navigate("/settings");
  };

  return (
    <Section>
      <Select
        header='Payment Method'
        value={formData.paymentMethod}
        onChange={(e) => handleInputChange("paymentMethod", e.target.value)}
      >
        {paymentMethods.map((method, index) => (
          <option key={index} value={method}>
            {method}
          </option>
        ))}
      </Select>
      <Select
        header='Fiat Money'
        value={formData.fiatCurrency}
        onChange={(e) => handleInputChange("fiatCurrency", e.target.value)}
      >
        {fiatCurrencies.map((currency, index) => (
          <option key={index} value={currency}>
            {currency}
          </option>
        ))}
      </Select>

      <Input
        header='Card number'
        placeholder='1111 1111 1111 1111'
        value={formData.cardNumber}
        onChange={(e) => handleInputChange("cardNumber", e.target.value)}
      />

      <Input
        header='Entering the amount'
        placeholder='0'
        value={formData.amount}
        onChange={(e) => handleInputChange("amount", e.target.value)}
        after={
          <Tappable
            Component='div'
            style={{
              display: "flex",
            }}
            onClick={() => handleInputChange("amount", "")}
          >
            <Icon20Cancel />
          </Tappable>
        }
      />

      <Button
        style={{ position: "fixed", bottom: 20, left: "5%", width: "90%" }}
        mode='filled'
        size='l'
        onClick={handleSave}
      >
        Save
      </Button>
    </Section>
  );
};

export default FormPm;
