import React from 'react'
import { NavLink } from 'react-router-dom'
import { Tabbar } from "@telegram-apps/telegram-ui";
import { TabbarItem } from "@telegram-apps/telegram-ui/dist/components/Layout/Tabbar/components/TabbarItem/TabbarItem";
import {
  Icon28GlobeOutline,
  Icon28QrCodeOutline,
  Icon28ServicesOutline,
} from "@vkontakte/icons";

const TabbarComponent = () => {
  return (
    <Tabbar style={{zIndex: 1}}>
        <NavLink to='/' style={{ textDecoration: "none" }} end>
          {({ isActive }) => (
            <TabbarItem text='Get money' selected={isActive}>
              <Icon28GlobeOutline />
            </TabbarItem>
          )}
        </NavLink>
        <NavLink to='/transactions' style={{ textDecoration: "none" }}>
          {({ isActive }) => (
            <TabbarItem text='Transactions' selected={isActive}>
              <Icon28QrCodeOutline />
            </TabbarItem>
          )}
        </NavLink>
        <NavLink to='/settings' style={{ textDecoration: "none" }}>
          {({ isActive }) => (
            <TabbarItem text='Settings' selected={isActive}>
              <Icon28ServicesOutline />
            </TabbarItem>
          )}
        </NavLink>
      </Tabbar>
  )
}

export default TabbarComponent