import React from "react";
import {
  Icon28MessageOutline,
  Icon24ArrowLeftOutline,
  Icon24DeleteOutline,
  Icon24ArrowRightOutline,
  Icon24Done,
} from "@vkontakte/icons";
import {
  Section,
  Cell,
  Switch,
  Title,
  LargeTitle,
  Avatar,
  IconButton,
  Info,
  Text,
  Badge,
  List,
  Subheadline,
  Snackbar,
} from "@telegram-apps/telegram-ui";
import {
  SwipeableList,
  SwipeableListItem,
  SwipeAction,
  TrailingActions,
  LeadingActions,
} from "react-swipeable-list";
import "react-swipeable-list/dist/styles.css";
import { Icon20Copy } from "@telegram-apps/telegram-ui/dist/icons/20/copy";
import Sort from "../components/Sort";
import TabbarComponent from "../components/TabbarComponent";

const amount = 652973.63;

const mockOrders = [
  {
    id: 1,
    type: "incoming",
    amount: 0.313,
    currency: "TON",
    accountNumber: "+78935985398000",
    status: "Paid",
    messages: 3,
  },
  {
    id: 2,
    type: "outgoing",
    amount: 0.313,
    currency: "BTC",
    accountNumber: "+78935985398000",
    status: "1:96",
    messages: 1,
  },
  {
    id: 3,
    type: "incoming",
    amount: 9.313,
    currency: "ETH",
    accountNumber: "+78935985398000",
    status: "Paid",
    messages: 1,
  },
];

const GetMoney: React.FC = () => {
  const [sortOrder, setSortOrder] = React.useState<"asc" | "desc">("asc");
  const [snackbar, setSnackbar] = React.useState<React.ReactNode>(null); // состояние для Snackbar

  const handleSort = () => {
    setSortOrder(prevSortOrder => prevSortOrder === "asc" ? "desc" : "asc");
  };

  const sortedOrders = [...mockOrders].sort((a, b) => {
    if (sortOrder === "asc") {
      return a.currency.localeCompare(b.currency);
    } else {
      return b.currency.localeCompare(a.currency);
    }
  });

  const handleCopy = (text: string) => {
    navigator.clipboard
      .writeText(text)
      .then(() => {
        setSnackbar(
          <Snackbar onClose={() => setSnackbar(null)}>Number copied!</Snackbar>
        );
      })
      .catch((err) => {
        console.error("Ошибка при копировании текста:", err);
      });
  };

  const formattedAmount = amount.toLocaleString("en-US", {
    style: "currency",
    currency: "USD",
  });
  const [dollars, cents] = formattedAmount.split(".");

  const trailingActionsDelet = () => (
    <TrailingActions>
      <SwipeAction
        destructive={true}
        onClick={() => {
          // обработка удаления
        }}
      >
        <div
          style={{
            justifyContent: "flex-end",
            alignItems: "center",
            display: "flex",
            color: "white",
            background: "red",
            paddingRight: "20px",
          }}
        >
          <Icon24DeleteOutline />
        </div>
      </SwipeAction>
    </TrailingActions>
  );

  const leadingActionsApprove = () => (
    <LeadingActions>
      <SwipeAction
        onClick={() => {
          // обработка принятия
        }}
      >
        <div
          style={{
            justifyContent: "flex-start",
            alignItems: "center",
            display: "flex",
            color: "white",
            background: "green",
            paddingLeft: "20px",
          }}
        >
          <Icon24Done />
        </div>
      </SwipeAction>
    </LeadingActions>
  );

  return (
    <>
      <Section style={{ paddingBottom: 100 }}>
        <Cell
          interactiveAnimation='opacity'
          after={<Switch defaultChecked />}
          Component='label'
          children={
            <div style={{ display: "flex", alignItems: "baseline" }}>
              <LargeTitle weight='1'>{dollars}.</LargeTitle>
              <Title level='2' weight='1'>
                {cents}
              </Title>
            </div>
          }
        />

        <List>
          <Cell
            interactiveAnimation='opacity'
            after={<Sort handleSort={handleSort} />}
            subtitle={
              <Text caps weight='3'>
                ACTIVE ORDERS
              </Text>
            }
          />
          <SwipeableList fullSwipe={true}>
            {sortedOrders.map((order) => (
              <SwipeableListItem
                key={order.id}
                threshold={0.2}
                trailingActions={trailingActionsDelet()}
                leadingActions={leadingActionsApprove()}
              >
                <Cell
                  style={{ flexGrow: 1 }}
                  interactiveAnimation='opacity'
                  before={
                    <Avatar
                      size={40}
                      src='https://avatars.githubusercontent.com/u/84640980?v=4'
                    />
                  }
                  after={
                    <Info type='text'>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "column",
                          alignItems: "center",
                        }}
                      >
                        <IconButton
                          style={{ background: "none" }}
                          mode='gray'
                          size='s'
                        >
                          <Icon28MessageOutline />
                          <Badge
                            style={{ marginLeft: -15 }}
                            mode='primary'
                            type='number'
                          >
                            {order.messages}
                          </Badge>
                        </IconButton>
                        {order.status === "Paid" ? (
                          <Badge
                            type='number'
                            style={{ marginTop: 2 }}
                            mode='white'
                          >
                            Paid
                          </Badge>
                        ) : (
                          <Subheadline
                            level='2'
                            weight='3'
                            style={{ marginRight: 11 }}
                          >
                            {order.status}
                          </Subheadline>
                        )}
                      </div>
                    </Info>
                  }
                  subtitle={
                    <span>
                      {order.accountNumber.slice(0, 10)}...
                      <Icon20Copy
                        onClick={() => handleCopy(order.accountNumber)}
                        style={{ cursor: "pointer", marginLeft: "5px" }}
                      />
                    </span>
                  }
                >
                  <Text style={{ display: "flex", gap: 10 }}>
                    {order.type === "incoming" ? (
                      <Icon24ArrowRightOutline fill='#32E55E' />
                    ) : (
                      <Icon24ArrowLeftOutline fill='#E937ED' />
                    )}
                    +{order.amount} {order.currency}
                  </Text>
                </Cell>
              </SwipeableListItem>
            ))}
          </SwipeableList>
        </List>
        {snackbar}
      </Section>
      <TabbarComponent />
    </>
  );
};

export default GetMoney;
