import React from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";
import { CellSection } from "./components/CellSection";
import GetMoney from "./pages/GetMoney";
import Settings from "./pages/Settings";
import Transactions from "./pages/Transactions";
import FormPm from "./pages/FormPm";
import FormEx from "./pages/FormEx";

export const App = () => {
  return (
    <Router>
      <Routes>
        <Route path='/' element={<GetMoney />} />
        <Route path='/transactions' element={<Transactions />} />
        <Route path='/settings' element={<Settings />} />
        <Route path='/settings/formPm' element={<FormPm />} />
        <Route path='/settings/formEx' element={<FormEx />} />
        <Route path='/cell-section' element={<CellSection />} />
      </Routes>
    </Router>
  );
};
