import React from "react";
import {Select, Section, Input, Button} from '@telegram-apps/telegram-ui'
import { useNavigate } from "react-router-dom";

const FormEx = () => {
  const navigate = useNavigate();

  const handleSave = () => {
    // console.log(formData);
    navigate("/settings");
  };
  return (
    <Section>
      <Select header='Exchange name'>
        <option>Hello</option>
        <option>Hello</option>
        <option>Hello</option>
      </Select>
      <Input header="Auth"/>
      <Button
        style={{ position: "fixed", bottom: 20, left: "5%", width: "90%" }}
        mode='filled'
        size='l'
        onClick={handleSave}
      >
        Save
      </Button>
    </Section>
  );
};

export default FormEx;
