import React from "react";
import { Icon16ArrowsUpDown } from "@vkontakte/icons";
import { IconButton, Text } from "@telegram-apps/telegram-ui";

interface SortProps {
  handleSort: () => void;
}

const Sort: React.FC<SortProps> = ({ handleSort }) => {
  return (
    <IconButton
      style={{ alignItems: "center", gap: 5 }}
      mode="plain"
      size="s"
      onClick={handleSort}
    >
      <Icon16ArrowsUpDown width={20} height={20} />
      <Text weight="2">Sort</Text>
    </IconButton>
  );
};

export default Sort;
