import "@telegram-apps/telegram-ui/dist/styles.css";
import "./index.css";

import React from "react";
import { createRoot } from "react-dom/client";
import { App } from "./App";
import { AppRoot } from "@telegram-apps/telegram-ui";
import { setBackgroundAsSecondary } from "./helpers/setBackgroundAsSecondary";

// Установка фонового цвета
setBackgroundAsSecondary();

// Создание корневого элемента React
const rootElement = document.getElementById("root");
if (rootElement) {
  const root = createRoot(rootElement);
  root.render(
    <AppRoot>
      <App />
    </AppRoot>
  );
} else {
  console.error(
    'Failed to find the root element. Ensure there is a div with id "root" in your HTML.'
  );
}
