import React from "react";
import { Link } from "react-router-dom";
import {
  Section,
  Cell,
  Avatar,
  ButtonCell,
  Text,
  Caption,
} from "@telegram-apps/telegram-ui";
import {
  Icon28AddCircleOutline,
  Icon28AddSquareOutline,
  Icon16ChevronOutline,
} from "@vkontakte/icons";

// Интерфейс для каждого элемента в списке
interface Item {
  name: string;
  balanceAmount: string;
  currency: string;
  avatar: string;
  onClick?: () => void;
}

// Интерфейс для пропсов компонента SettingsSection
interface SettingsSectionProps {
  buttonType: "payment" | "stocks";
  buttonLink: string;
  captionText: string;
  items: Item[];
  showCaption?: boolean;
  footerBlockText: string;
}

// Компонент SettingsSection
const SettingsSection: React.FC<SettingsSectionProps> = ({
  buttonType,
  buttonLink,
  captionText,
  items,
  showCaption = true,
  footerBlockText,
}) => {
  const buttonIcon =
    buttonType === "payment" ? (
      <Icon28AddCircleOutline />
    ) : (
      <Icon28AddSquareOutline />
    );
  const buttonText =
    buttonType === "payment" ? "Add Payment Method" : "Add Stocks";

  return (
    <>
      <Section
        style={{ borderRadius: "12px", overflow: "hidden", marginBottom: 28 }}
      >
        <Link to={buttonLink} style={{ textDecoration: "none" }}>
          <ButtonCell
            before={buttonIcon}
            style={{ padding: "8px 18px", display: "flex", flexGrow: 1 }}
          >
            <Text weight="2">{buttonText}</Text>
          </ButtonCell>
        </Link>
      </Section>

      {showCaption && (
        <Caption
          caps
          level="1"
          weight="3"
          style={{ opacity: 0.6, margin: "0 16px 8px", display: "block" }}
        >
          {captionText}
        </Caption>
      )}

      <Section style={{ borderRadius: "12px", overflow: "hidden" }}>
        {items.map((item, index) => (
          <Cell
            key={index}
            onClick={item.onClick}
            before={<Avatar size={28} src={item.avatar} />}
            after={
              <Text
                style={{
                  display: "flex",
                  alignItems: "center",
                  gap: 8,
                  opacity: 0.6,
                  textWrap: "nowrap",
                }}
              >
                {item.balanceAmount} {item.currency} <Icon16ChevronOutline />
              </Text>
            }
          >
            {item.name}
          </Cell>
        ))}
      </Section>
      <Caption
        level="1"
        weight="3"
        style={{ opacity: 0.6, margin: "0 16px 24px", display: "block" }}
      >
        {footerBlockText}
      </Caption>
    </>
  );
};

export default SettingsSection;
export type { Item };
